export default {
	data() {
		return {
			imgUrl:getApp().globalData.imgUrl,
			showMsg:false,
			tabId:1,
			UnExchangePage:1,
			exchangePage:1,
			next:true,
			unNext:true,
			list:'',
			unList:'',
			unNum:0,
			num:0,
			exchangeMsg:""
		};
	},
	onShow() {
		console.log(this.$Store.state.vjfAppid)
		if(!getApp().globalData.vjfOpenid){
			if(this.$Store.state.vjfAppid){
				this.$Store.dispatch('getVjfOpenid').then(res=>{
					let [e,r] = res;
					if(r){
						getApp().globalData.vjfOpenid = r.data.vjfOpenid;
						this.queryExchange();
						this.queryUnExchange();
					}else{
						console.log(e)
						uni.navigateTo({
							url: '../tool/getOpenid/getOpenid?type=vjfOpenid'
						})
					}
				})
			}
		}else{
			this.queryExchange();
			this.queryUnExchange();
		}
	},
	methods:{
		tab(id){
			this.tabId=id;
		},
		toIntroduction(){
			uni.navigateTo({
				url:'introduction'
			})
		},
		toRule(){
			uni.navigateTo({
				url:'rule'
			})
		},
		toMap(){
			uni.navigateTo({
				url:'storeList'
			})
		},
		queryUnExchange(){
			uni.showLoading({
				title:'加载中'
			})
			uni.request({//未兑换
				url:this.$Store.state.requestUrl_lx + '/consumer/queryUnExchangePrizeLst',
				method:'POST',
				data:{
					"companyKey":this.$Store.state.companyKey,
					"vjifenOpenid":getApp().globalData.vjfOpenid,
					"currentPage":this.UnExchangePage,
					"count":20
				}
			}).then(res=>{
				uni.hideLoading()
				console.log(res)
				let [e,r] = res;
				if(r){
					if(r.data.reply&&(!r.data.reply.prizeAry||r.data.reply.prizeAry.length<20)){
						this.unNext = false;
					}else{
						this.unNext = true;
					}
					if(this.UnExchangePage==1){
						this.unList = r.data.reply.prizeAry;
						this.unNum = r.data.reply.prizeNum;
					}else{
						if(r.data.reply.prizeAry&&r.data.reply.prizeAry.length>0){
							this.unList = this.unList.concat(r.data.reply.prizeAry);
						}
					}
				}else{
					console.log(e);
					uni.showModal({
						title:'提示',
						content:'系统繁忙',
						showCancel:false
					})
				}
			}).catch(err=>{
				uni.hideLoading();
				console.log(err)
				uni.showModal({
					title:'提示',
					content:'系统繁忙',
					showCancel:false
				})
			})
		},
		queryExchange(){
			uni.showLoading({
				title:'加载中'
			})
			uni.request({//未兑换
				url:this.$Store.state.requestUrl_lx + '/consumer/queryExchangedPrizeLst',
				method:'POST',
				data:{
					"companyKey":this.$Store.state.companyKey,
					"vjifenOpenid":getApp().globalData.vjfOpenid,
					"currentPage":this.exchangePage,
					"count":20
				}
			}).then(res=>{
				uni.hideLoading()
				console.log(res)
				let [e,r] = res;
				if(r){
					if(r.data.reply&&(!r.data.reply.prizeAry||r.data.reply.prizeAry.length<20)){
						this.next = false;
					}else{
						this.next = true;
					}
					if(this.exchangePage==1){
						this.list = r.data.reply.prizeAry;
						this.num = r.data.reply.prizeNum;
					}else{
						if(r.data.reply.prizeAry&&r.data.reply.prizeAry.length>0){
							this.list = this.list.concat(r.data.reply.prizeAry);
						}
					}
				}else{
					console.log(e);
					uni.showModal({
						title:'提示',
						content:'系统繁忙',
						showCancel:false
					})
				}
			}).catch(err=>{
				uni.hideLoading();
				console.log(err)
				uni.showModal({
					title:'提示',
					content:'系统繁忙',
					showCancel:false
				})
			})
		},
		lower(){//上拉加载
			if(this.next&&this.tabId==2){
				this.exchangePage++;
				this.queryExchange();
			} else if(this.unNext&&this.tabId==1){
				this.UnExchangePage++;
				this.queryUnExchange();
			} else {
				uni.showToast({
					title:'没有更多了记录了',
					icon:'none'
				})
			}
			
		},
		queryDetails(index){
			uni.showLoading({
				title:'加载中'
			})
			uni.request({//未兑换
				url:this.$Store.state.requestUrl_lx + '/consumer/exchangedPrizeDetail',
				method:'POST',
				data:{
					"companyKey":this.$Store.state.companyKey,
					"vjifenOpenid":getApp().globalData.vjfOpenid,
					"terminalKey":this.list[index].terminalKey,
					"exchangeNo":this.list[index].exchangeNo
				}
			}).then(res=>{
				uni.hideLoading();
				let [e,r] = res;
				if(r){
					this.exchangeMsg = r.data.reply;
					if(this.exchangeMsg.exchangeSku[0].exchangeTime){
						this.exchangeMsg.exchangeSku[0].exchangeTime = this.exchangeMsg.exchangeSku[0].exchangeTime.split('.')[0]
					}
					this.showMsg = true;
				}else{
					console.log(e);
					uni.showModal({
						title:'提示',
						content:'系统繁忙',
						showCancel:false
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				console.log(err)
				uni.showModal({
					title:'提示',
					content:'系统繁忙',
					showCancel:false
				})
			})
		}
	}
}
